.megamenu-list {
  li {
      padding: 5px 20px;
      position: relative;

      a {
          color: $menu-item;

          &:hover {
              color: $primary;
          }
      }

      &:before {
          content: "\ea1d";
          position: absolute;
          left: 0;
          font-family: $boxIcons;
      }
  }
}

@media (min-width: 600px) {
  .dropdown-menu-lg {
      width: 320px;
  }
}
@media (max-width: 600px) {
  .navbar-header {
    .dropdown {
      position: static;
      .dropdown-menu {
        left: 10px!important;
        right: 10px!important;
      } 
    }
  }
}